export interface IPrice {
    rate: number;
    currency: "USD";
}

export interface ITokenInfo {
    symbol: string;
    decimals: string | number;
    price: false | IPrice;
}

export interface IToken {
    tokenInfo: ITokenInfo;
    balance: number;
}

export class GetAddressInfoResponse {
    public tokens!: IToken[];
}
